:root {
  --bg-primary: #ffffff;
  --text-primary: #333333;
  --text-secondary: #666666;
  --nav-bg: rgba(255, 255, 255, 0.8);
  --clr-accent: #0066cc;
  --clr-accent-light: rgba(0, 102, 204, 0.2);
  --card-bg: rgba(255, 255, 255, 0.8);
  --card-border: rgba(0, 0, 0, 0.1);
  --shadow: rgba(0, 0, 0, 0.1);
  --shadow-hover: rgba(0, 0, 0, 0.2);
  --glow-effect: 0 0 10px rgba(255, 255, 255, 0.5);
  --fs-h1: clamp(1.5rem, 3vw, 2.5rem);
  --fs-h2: clamp(1.5rem, 3vw, 2.5rem);
  --fs-h3: clamp(1.25rem, 2.5vw, 2rem);
  --fs-body: clamp(0.9rem, 1.8vw, 1rem);
  --fw-reg: 300;
  --fw-bold: 500;
  --card-gradient: linear-gradient(135deg, rgba(84, 185, 255, 0.1), rgba(128, 0, 128, 0.1));
  --card-bg-color: rgba(255, 255, 255, 0.2);
}

[data-theme='dark'] {
  --bg-primary: #1a1a1a;
  --text-primary: #ffffff;
  --text-secondary: #cccccc;
  --nav-bg: rgba(26, 26, 26, 0.8);
  --clr-accent: #4d9fff;
  --clr-accent-light: rgba(77, 159, 255, 0.2);
  --card-bg: rgba(30, 30, 30, 0.8);
  --card-border: rgba(255, 255, 255, 0.1);
  --shadow: rgba(0, 0, 0, 0.3);
  --shadow-hover: rgba(0, 0, 0, 0.4);
  --glow-effect: 0 0 15px rgba(76, 201, 240, 0.6);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, html {
  overflow-x: hidden; /* Prevent horizontal scrolling */
  scroll-behavior: smooth;
}

body {
  font-family: 'Arial', sans-serif;
  color: var(--text-primary);
  background: url('/public/blue-gradient.jpg') no-repeat center center fixed;
  background-size: cover;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3 {
  line-height: 1.1;
  margin: 0;
}

section {
  padding: 3em 1em;
  width: 100%;
  position: relative;
  overflow: hidden;
}

@media (max-width: 600px) {
  body {
    font-size: 0.9rem;
  }

  .card {
    padding: 15px;
    box-shadow: 0 2px 10px var(--shadow);
  }

  .glass-container {
    padding: 15px;
  }

  h1, h2, h3 {
    font-size: 1.5rem;
  }
}

@media (max-width: 768px) {
  section {
    padding: 2em 1em;
  }

  .card {
    padding: 20px;
    box-shadow: 0 3px 15px var(--shadow);
  }

  h1, h2, h3 {
    font-size: 2rem;
  }
}

@media (min-width: 992px) {
  .card {
    padding: 25px;
    box-shadow: 0 4px 20px var(--shadow);
  }

  h1, h2, h3 {
    font-size: 2.5rem;
  }
}

.card {
  background: var(--card-gradient);
  border-radius: 10px;
  box-shadow: 0 4px 20px var(--shadow);
  transition: box-shadow 0.3s ease, transform 0.3s ease;
  padding: 20px;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 20px var(--glow-effect), 0 10px 25px var(--shadow-hover);
}

.glass-container {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  padding: 20px;
  position: relative;
  overflow: hidden;
}

.moving-shapes {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('/path/to/shapes.svg') no-repeat center center;
  animation: moveShapes 20s linear infinite;
}

@keyframes moveShapes {
  0% { transform: translate(0, 0); }
  100% { transform: translate(-100px, -100px); }
}

.floating-shape {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  animation: float 10s infinite ease-in-out;
  opacity: 0.7;
  backdrop-filter: blur(10px);
}

.shape1 {
  width: 100px;
  height: 100px;
  top: 10%;
  left: 20%;
}

.shape2 {
  width: 150px;
  height: 150px;
  top: 50%;
  left: 70%;
}

.shape3 {
  width: 80px;
  height: 80px;
  top: 80%;
  left: 40%;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

